body {
    background-color: #fffcf5;
    margin: 0;
}

.wrapper {
    display: flex;
    justify-content: space-between;
}

.logo {
    font-family: 'Gistesy';
}

.about {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 100px;
}

.sub {
    font-family: 'Garet';
    font-weight: bolder;
}

.name {
    color: #fa573e;
    font-family: 'Gistesy';
    font-size: 140px;
    font-weight: lighter;
    margin: 0;
    padding: 40px 0 40px 0;
}

.me {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 20px;
}

.profile {
    width: 75%;
    height: auto;
}

.image {
    max-width: 100%;
    max-height: 100%;
}

@media screen and (max-width: 1000px) {
    .image {
        display: none;
    }
}

.resume {
    width: 20%;
    margin-top: 10px;
    padding: 9px;
    background-color: white;
    border: 1px solid #fa573e;
    border-radius: 5px;
}

.resume:hover {
    cursor: pointer;
    background-color: #fa573e;
    color: white;
}

.social {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    font-size: 30px;
}

a,
a:visited,
a:hover,
a:active {
    text-decoration: none;
    color: inherit;
}

.badges {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
}

.badge {
    margin-right: 8px;
}

.linkedin {
    margin-right: 10px;
}

.github {
    margin-right: 10px;
}

.dev {
    margin-right: 10px;
}
